
    @navbar-height: 95px; 
    @main-container-margin: ( @navbar-height + 30px );
    @micro_spacer: 2px;

    @main_window_pane_height: 565px;

    @text_color: #3d3d3d;

    body, h1, h2, h3, h4, h5, h6, button, a, p {
        
        font-family: 'Roboto', sans-serif;

    }

    html, body {

        height: 100%;
        width: 100%;

    }


    /* Navbar */

        div.navbar-header {

            position: absolute;

            a.navbar-brand {

                padding: 7px;

                img {

                    padding: 3px;
                    margin: 3px;

                }  

            }

        }

        #header_text {

            color: #363636;

        }

        .navbar-default {

            box-shadow: none;
            background-color: #ffffff;

            background-image: url(/images/header/bottom_bars.jpg);
            height: @navbar-height;

        }

        .navbar-fixed-top {

            border-width: 0 0 0 0;

        }

    /* End Navbar */


    /* Main Container */

        /* LEFT Side Bar Icons */
        #left-sidebar .new {
          background-image: url(/images/sidebar/new.png);
        }
        #left-sidebar .load {
          background-image: url(/images/sidebar/load.png);
        }
        #left-sidebar .compare {
          background-image: url(/images/sidebar/compare.png);
        }
        #left-sidebar .save {
          background-image: url(/images/sidebar/save.png);
        }
        /* END LEFT Side Bar Icons */

        /* RIGHT Side Bar Icons */
        #right-sidebar .materials {
          background-image: url(/images/sidebar/materials.png);
        }
        #right-sidebar .colors {
          background-image: url(/images/sidebar/colors.png);
        }
        #right-sidebar .patterns {
          background-image: url(/images/sidebar/patterns.png);
        }
        #right-sidebar .gradients {
          background-image: url(/images/sidebar/gradients.png);
        }
        #right-sidebar .texts {
          background-image: url(/images/sidebar/texts.png);
        }
        #right-sidebar .numbers {
          background-image: url(/images/sidebar/numbers.png);
        }
        #right-sidebar .graphics {
          background-image: url(/images/sidebar/graphics.png);
        }
        #right-sidebar .sizes {
          background-image: url(/images/sidebar/sizes.png);
        }
        
        /* END RIGHT Side Bar Icons */




        #main_container {

            height: 80%;
            margin-top: @main-container-margin;

            #main-row {

                height: 100%;

                #left-pane-column {

                    height: 100%;

                }

                #right-pane-column {

                    height: 100%;

                }

            }

        }


    /* End Main Container */

    /* Generic Panes */

        div.pane {
        
            div.pane-top {

                width: 90%;
                text-align: center;

                button {

                    margin-bottom: 3px;

                }

            }

            div.pane-sidebar {

                float: left;
                text-align: center;
                width: 10%;
                padding-right: @micro_spacer;

                a.sidebar-buttons {

                    background-color: #acacac;
                    width: 41px;
                    height: 41px;
                    display: block;
                    text-align: center;
                    margin-bottom: @micro_spacer;
                    padding-top: 2px;
                    background-position: center; 
                    background-repeat: no-repeat;
                    float: right;
                    background-size: 75% 75%;

                }

                a.sidebar-buttons:hover {

                    color: white;

                }

            }

            div.pane-main-window {

                float: right;
                background-color: #fff;

                width: 90%;
                height: @main_window_pane_height;

                border: 1px solid #b3b3b3;

            }


        }

    /* End Generic Panes */

    /* UI Generic */

        a.active_button {

            background-color: #3d3d3d !important;

        }

    /* End UI Generic */


    /* Left Pane - Main Canvas */

        #left-pane {

            #left-main-window {

                overflow: hidden;

            }

        }

        #left-main-window {

            canvas#canvas-views {

                position: absolute;

            }

        }

    /* End Left Pane - Main Canvas */

    /* Camera Views */

        #camera-views {

            float: right;
            text-align: center;
            width: 90%;
            padding: 3px;
            position: relative;
            margin-top: -73px;

            a {

                color: @text_color;
                width: 63px;
                height: 70px;
                text-align: center;
                padding: 0;

                img {

                    width: 100%;
                    height: 100%;

                }

            }

        }

    /* End Camera Views */

    /* Right Pane - Property Customizer */
        
        #right-pane {



        }

    /* End Right Pane - Property Customizer */

    
    /* Options Panel */

        div.options_panel  {

            div.options_panel_header {

             
                background-color: #f2f2f2;
                width: 100%;
                display: table;
                height: 40px;
                border-bottom: 1px solid #b3b3b3;
                padding: 10px;

                span.header_text {
                    
                    font-weight: bold;
                    padding: 10px;
                    color: @text_color;
                    height: 100%;

                    border-right: 1px solid #b3b3b3;
                    padding: 10px;
                    padding-right: 23px;

                }


            }


            div.options_panel_section {

                width: 100%;
                display: table;
                height: 40px;
                border-bottom: 1px solid #b3b3b3;
                padding: 10px;

                -moz-transition: all .2s;
                -webkit-transition: all .2s;
                transition: all .2s;

                span.text {
                    
                    font-weight: bold;
                    padding: 10px;
                    color: @text_color;
                    height: 100%;
                    padding: 10px;
                    padding-right: 23px;
                    display: table-cell;

                }

                label {

                    color: @text_color;
                    width: 20%;
                    font-weight: normal;
                    padding-left: 10px;

                }

                button {

                    width: 20%;
                    font-size: 75%;

                }

                div.color_panel_container {

                    margin-left: 23px;
                    margin-top: 23px;

                }    


            }

            #color_panel_section {

                padding-bottom: 30px;

            }

        }

        div.color_element {

            width: 43px;
            height: 43px;
            background-image: url(/images/sidebar/color-bg-0.png);
            float: left;
            background-repeat: no-repeat;

        }

        button.modifier {

            margin-right: 10px;

        }

        /* Gradients */

        div.gradient_element {

            width: 70px;
            height: 70px;
            float: left;
            background-repeat: no-repeat;
            margin: 10px;

            button {
                border-radius: 0px;
            }

        }

        div.gradient_panel_container {

            margin-left: 50px;
            display: table;

        }

        /* End Gradients */


    /* End Options Panel */

    /* Right Main Window */

        
        #right-main-window {

            overflow-y: scroll;

        }


    /* End Right Main Window */    

    /* Popovers */

        .popover {

            width: 415px;
            height: 190px;
            max-width: 100%;
            left: 105px !important;

        }

        #popover_color_panel {

            width: 412px;

            div.color_element {

                float: left;

            }

        }

        div.popper {

            width: 35px;
            height: 35px;
            border-radius: 8px; 
            border: 2px solid white;

            background-color: #d31145;

        }

        a.popper_button {
            padding: 0;
            background-image: none;
            background-color: none;
            border: none;
            box-shadow: none;
        }

        a.popper_button:focus {

            background-color: #acacac;

        }



    /* End Popovers */


    /* Canvas */

        canvas#front_view, canvas#back_view, canvas#left_view, canvas#right_view {

            width: 100%;
            height: 100%;

        }

    /* End Canvas */


    /* Pickers */

        div.picker_container {

            padding: 30px;
            background-color: #f8f8f8;
            height: 550px;

        }

        div.sports_categories {

            display: block;
            width: 130px;
            height: 130px;

            float: left;
            background-color: #7f7f7f;
            margin-left: 8px;
            margin-bottom: 30px;
            padding: 10px;
            text-align: center;

            border: solid 1px #b3b3b3;

            span.categories_label {

                display: block;
                margin-top: 120px;
                padding: 3px;
                
            }

            background-position: center; 
            background-size: 100% 100%;
            -moz-transition: all .2s;
            -webkit-transition: all .2s;
            transition: all .2s;

            filter:grayscale(100%);
            webkit-filter:grayscale(100%);

        }

        div.sports_categories_highlighted {

            border: solid 1px #acacac;
            background-size: 130% 130%;

            filter:grayscale(0%);
            webkit-filter:grayscale(0%);

        }

        div.sports_categories_activated {

            border: solid 1px #acacac;

            span.categories_label {

                display: block;
                margin-top: 80px;
                padding: 3px;
                background-color: #acacac;

                color: white;

                opacity: 0.9;

                border-bottom: solid 1px #ffffff;

            }

            background-size: 100% 100%;
            filter:grayscale(0%);
            webkit-filter:grayscale(0%);

        }

        div.picker_container {

            strong#active_sports_category {

                font-weight: bold;
                color: #3d3d3d;
                height: 100%;
                padding: 10px;
                padding-right: 23px;

                width: 100%;
                display: table;
                height: 40px;
                border-bottom: 1px solid #b3b3b3;
                padding: 10px;

            }

        }

        div.style_entry {

            display: block;
            width: 130px;
            height: 130px;

            float: left;
            background-color: #ffffff;
            margin-left: 8px;
            margin-bottom: 30px;
            padding: 10px;
            text-align: center;

            border: solid 1px #b3b3b3;

            span.style_label {

                display: block;
                margin-top: 120px;
                padding: 3px;
            }

            background-position: center; 
            background-size: 100% 100%;
            -moz-transition: all .2s;
            -webkit-transition: all .2s;
            transition: all .2s;

            filter:grayscale(100%);
            webkit-filter:grayscale(100%);


        }

        div.style_entry_highlighted {

            border: solid 1px #acacac;
            background-size: 130% 130%;
            filter:grayscale(0%);
            webkit-filter:grayscale(0%);

        }

         div.gender_picker {

            display: block;
            width: 130px;
            height: 130px;

            float: left;
            background-color: #7f7f7f;
            margin-left: 8px;
            margin-bottom: 10px;
            padding: 10px;
            text-align: center;

            border: solid 1px #b3b3b3;

            span.style_label {

                display: block;
                margin-top: 80px;
                padding: 3px;
                background-color: #3d3d3d;
                color: #b3b3b3;

                opacity: 0.9;

                border-bottom: solid 2px grey;

            }

            background-position: center; 
            background-size: 100% 100%;
            
            -moz-transition: all .2s;
            -webkit-transition: all .2s;
            transition: all .2s;

            filter:grayscale(100%);
            webkit-filter:grayscale(100%);


        }

        div.gender_picker_highlighted {

            border: solid 1px #acacac;

            span.style_label {

                display: block;
                margin-top: 80px;
                padding: 3px;
                background-color: #acacac;

                color: white;

                opacity: 0.9;

                border-bottom: solid 2px #fff;

            }

            background-size: 130% 130%;

            filter:grayscale(0%);
            webkit-filter:grayscale(0%);
        }


        /* GENDER PICKER */

            div.gender_picker {
                float: none;
                margin-left: 0px;
            }

            div.gender_picker_header {
                border-bottom: 1px solid #b3b3b3;
                width: 100%;
                padding-bottom: 3px;
                margin-bottom: 10px;
            }

            div.picker_header {
                width: 100%;
                display: block;
                height: 33px;
                border-bottom: 1px solid #b3b3b3;
                padding-bottom: 3px;
                margin-bottom: 10px;

                span {
                    
                    float: left;
                    
                }

                button {
                    float: right;
                    background: none;
                    border: none;                    
                }

                button.button_tabs {
                    float: none;
                    border: solid 1px #353536;
                    margin-right: 3px;

                    -webkit-border-radius: 3px;
                    -moz-border-radius: 3px;
                    border-radius: 3px;
                }

                button.button_tabs:focus { outline: 0; }

            }    

            div.picker_header_tabs {
                padding-bottom: 40px;
            }


        /* END GENDER PICKER */

        /* MISC */

            div.no_designs {
                margin-top: 30px;
                border-top : 1px solid #b3b3b3;
                padding-top: 10px;
            }

            div.down_arrow {

                background-image: url('/images/sidebar/down-arrow.png');
                background-position: center;
                display: block;
                width: 20px;
                height: 13px; 

            }

            button.tether_button {

                background-color: #353536;
                color: #f8f8f8;
                border: none;
                -webkit-border-radius: 3px;
                -moz-border-radius: 3px;
                border-radius: 3px;

            }

            img.highlighter {



            }

            .user-profile { margin-top: -40px;}

            .gradient_slider {

                margin: 20px;
                height: 7px;

            }

            .ui-state-default, .ui-widget-content .ui-state-default, .ui-widget-header {
                border-radius: 10px;
                -webkit-border-radius: 10px;
                -moz-border-radius: 10px;
                width: 15px;
                height: 15px;
            }
            
            .gradient_slider_angle {
                margin: 0 auto;
                height: 155px;
            }

            .rs-handle {
                width: 15px !important;
                height: 15px !important;
            }

            .gradient_button {
                display: none;
            }

            .range_container {
               
                position: absolute;
                top: 0;
                bottom: 0;
                width: 100%;

                overflow:hidden;

            }

           .range {

               -ms-transform: rotate(-90deg); /* IE 9 */
                -webkit-transform: rotate(-90deg); /* Chrome, Safari, Opera */
                transform: rotate(-90deg);
                height: 420px;
                background-size: cover;
            
            }

            .rs-bg-color {

                background-size: cover;
            
            }

            .rs-seperator {
                border: 0px solid #aaa;
            }
         

        /* END MISC */

    /* End Pickers */

